html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Space Mono", monospace;
  min-height: 100vh;
  background-color: #c5e4e7;
}

h1,
h2,
h3 {
  margin-top: none;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  max-width: 28.125rem;
  margin: 0 auto;
}
@media (min-width: 40em) {
  .container {
    max-width: 37.5rem;
    padding: 0 1rem;
  }
}
@media (min-width: 64em) {
  .container {
    max-width: 53.125rem;
    padding: 0 1rem;
  }
}

.header {
  text-align: center;
  margin: 2.5rem auto 2rem;
}
.header__logo {
  object-fit: contain;
}
@media (min-width: 64em) {
  .header {
    margin-bottom: 4rem auto 3rem;
  }
}

.calc__container {
  background-color: white;
  padding: 1.5rem 1rem 2rem;
  border-radius: 1rem 1rem 0 0;
}
@media (min-width: 64em) {
  .calc__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    padding: 1.5rem;
    border-radius: 1rem;
  }
}
.calc input {
  height: 2.67375rem;
}

.form {
  padding: 0.5rem;
  margin-bottom: 2rem;
}
@media (min-width: 64em) {
  .form {
    margin-bottom: 0;
  }
}
.form label,
.form legend {
  display: inline-block;
  color: #5e7a7d;
  font-weight: 700;
  margin: 0 0 0.4rem;
  font-size: 0.9375rem;
}
.form input[type=text] {
  box-sizing: border-box !important;
  text-align: right;
  width: 100%;
  padding: 0.4rem 0.8rem;
  color: #00494d;
  font-size: 1.5rem;
  font-weight: 700;
  background-color: #f4fafa;
  border: 2px solid #f4fafa;
  border-radius: 0.375rem;
  outline: none;
}
.form input[type=text]:focus, .form input[type=text]:hover {
  border: 2px solid #26c0ab;
}
.form input:-webkit-autofill,
.form input:-webkit-autofill:hover,
.form input:-webkit-autofill:focus,
.form textarea:-webkit-autofill,
.form textarea:-webkit-autofill:hover,
.form textarea:-webkit-autofill:focus,
.form select:-webkit-autofill,
.form select:-webkit-autofill:hover,
.form select:-webkit-autofill:focus {
  border: 2px solid #26c0ab;
  -webkit-text-fill-color: none;
  background-color: #f4fafa;
  -webkit-text-fill-color: #00494d !important;
  transition: background-color 5000s ease-in-out 0s;
}

.input-group {
  margin: 0;
}
.input-group span {
  display: block;
  position: relative;
}
.input-group img {
  position: absolute;
  top: 50%;
  padding: 0rem 0.8rem;
  transform: translateY(-50%);
}

.tip-percent {
  border: none;
  padding: 0;
  margin: 1.8rem 0 1.5rem;
}
.tip-percent__list {
  list-style: none;
  padding: 0.3rem 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
@media (min-width: 40em) {
  .tip-percent__list {
    grid-template-columns: repeat(3, 1fr);
  }
}
.tip-percent__list-item label {
  cursor: pointer;
  width: 100%;
  margin: 0;
  height: 2.67375rem;
  padding: 0.2125rem 0;
  text-align: center;
  font-size: 1.5rem;
  background-color: #00494d;
  color: white;
  border-radius: 0.375rem;
  transform: all ease-our 0.3s;
}
.tip-percent__list-item label:hover {
  background-color: #26c0ab;
  color: #00494d;
}
.tip-percent__list-item input[type=radio] {
  display: none;
}
.tip-percent__list-item input[type=radio]:checked + label {
  background-color: #26c0ab;
  color: #00494d;
}
.tip-percent__list-item input#custom:checked + label {
  display: none;
}
.tip-percent__list-item input#custom:not(:checked) ~ input[type=text] {
  display: none;
}
.tip-percent__list-item input#custom:checked ~ input[type=text] {
  display: inline-block;
}

.result {
  padding: 1rem 1.5rem;
  background-color: #00494d;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
}
.result__values {
  padding: 0.3rem 0;
  flex-grow: 1;
}
.result__values div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.3rem 0;
  margin-bottom: 0.5rem;
}
.result__tag {
  margin: 0;
  color: white;
  font-weight: 500;
  line-height: 1.3;
}
.result__tag span {
  font-size: 0.8rem;
  font-weight: 700;
  color: #5e7a7d;
}
.result__value {
  margin: 0;
  font-size: 1.875rem;
  font-weight: 700;
  color: #26c0ab;
}
@media (min-width: 64em) {
  .result__value {
    font-size: 2.375rem;
  }
}
.result__reset {
  width: 100%;
  margin: 0.5rem 0;
  padding: 0.4rem 0.8rem;
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: 700;
  border: none;
  outline: none;
  background-color: #26c0ab;
  color: #00494d;
  border-radius: 0.375rem;
  line-height: 1;
}
.result__reset:hover {
  background-color: #c5e4e7;
}